ouch;\n    /*overflow-scrolling: touch;*/\n    -moz-overflow-scrolling: touch;\n    overflow: auto;\n    position: relative;\n    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0), 0 5px 15px 0 rgba(0, 0, 0, 0);\n    transition: box-shadow 350ms;\n}\n\nmain.show::before {\n    content: '';\n    display: block;\n    position: absolute;\n    top: -84px;\n    left: 0;\n    width: 100%;\n    height: 84px;\n    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.05), 0 5px 15px 0 rgba(0, 0, 0, 0.1);\n}\n\nmain .content {\n    margin: auto;\n    max-width: 400px;\n}\n\nh1,\nh2,\nh3,\nh4,\np,\nli {\n    color: #2c2e2f;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-weight: 400;\n}\n\nh1 {\n    font-size: 30px;\n    margin-bottom: 16px;\n    line-height: 42px;\n    font-family: PayPalSansBig, Helvetica, Arial, Sans-Serif !important;\n    font-weight: 200;\n}\n\nh2 {\n    font-size: 20px;\n    line-height: 24px;\n    text-align: center;\n    margin-bottom: 20px;\n}\n\nh3 {\n    font-size: 15px;\n    font-weight: 600;\n    margin-bottom: 6px;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n}\n\nh1.title {\n    text-align: center;\n    margin-top: 13px;\n}\n\nsvg {\n    margin: auto;\n    display: block;\n}\n\nh4 {\n    font-size: 15px;\n    line-height: 18px;\n    font-weight: bold;\n}\n\np {\n    font-size: 13px;\n    line-height: 18px;\n}\n\np.text-large {\n    font-size: 15px;\n    line-height: 24px;\n    font-family: PayPalSansBig, Helvetica, Arial, Sans-Serif;\n    font-weight: 200;\n    letter-spacing: 0;\n}\n\np.first-paragraph {\n    padding-bottom: 4px;\n}\n\na {\n    text-decoration: none;\n    color: #0070ba;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-weight: 600;\n}\n\nul {\n    padding-left: 0;\n}\n\nul.spaced li:not(:last-child) {\n    margin-bottom: 24px;\n}\n\nli {\n    list-style-type: disc;\n    margin-left: 18px;\n    font-size: 13px;\n    line-height: 18px;\n    letter-spacing: 0;\n}\n\n.cart-svg {\n    text-align: center;\n    display: block;\n    margin: 36px auto;\n}\n\n.divider {\n    height: 1px;\n    background-color: #eaeced;\n    margin-bottom: 21px;\n    margin-top: 8px;\n    width: 100%;\n}\n\n.anchor-medium {\n    font-size: 15px;\n    margin-bottom: 18px;\n    color: #2c2e2f;\n}\n\n.anchor-medium a {\n    font-size: 15px;\n}\n\n.anchor-small {\n    font-size: 13px;\n    margin-bottom: 16px;\n    color: #2c2e2f;\n}\n\n.anchor-small a {\n    font-size: 13px;\n}\n\n.show .modal__wrapper {\n    transform: translateY(0);\n}\n\n.show .modal__overlay {\n    opacity: 1;\n}\n\n.modal__container {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    overflow: hidden;\n}\n\n.modal__overlay {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background: rgba(108, 115, 120, 0.85);\n    transition: opacity 350ms;\n    opacity: 0;\n    z-index: -1;\n}\n\n.modal__overlay-side {\n    position: absolute;\n    top: 0;\n    height: 100%;\n    z-index: 2;\n}\n\n.modal__wrapper {\n    position: absolute;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    transition: transform 350ms;\n    transform: translateY(100%);\n}\n\n.modal__header-wrapper {\n    display: table;\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    z-index: 1000;\n}\n\n.modal__header-wrapper::after {\n    content: '';\n    display: table-cell;\n    visibility: hidden;\n    overflow-y: scroll;\n}\n\n.modal__header-container {\n    display: table-cell;\n    width: 100%;\n}\n\n.modal__header {\n    background: rgba(0, 0, 0, 0);\n    text-align: center;\n    height: 60px;\n    padding-bottom: 12px;\n    width: 100%;\n    max-width: 612px;\n    margin: 0 auto;\n    position: relative;\n    overflow: hidden;\n    height: 100px;\n}\n\n.modal__content-wrapper {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    overflow-y: scroll;\n}\n\n.modal__content-background {\n    width: 100%;\n    max-width: 612px;\n    margin: auto;\n    background: white;\n    min-height: 100%;\n}\n\n.modal__content {\n    position: relative;\n    z-index: 50;\n    padding-top: 84px;\n    overflow: hidden;\n}\n\n.header-shadow {\n    background: tomato;\n    width: 100%;\n    height: 10px;\n    -webkit-linear-gradient: (top, rgba(0, 0, 0, 0.06) 0%, rgba(0, 0, 0, 0) 100%);\n    background: -moz-linear-gradient(top, rgba(0, 0, 0, 0.06) 0%, rgba(0, 0, 0, 0) 100%);\n    /* FF3.6-15 */\n    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.06) 0%, rgba(0, 0, 0, 0) 100%);\n    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */\n    opacity: 0;\n}\n\n.header-shadow.show {\n    opacity: 1;\n}\n\n.accordion {\n    position: relative;\n}\n\n.accordion h3 {\n    margin: 0;\n}\n\n.accordion p {\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: 13px;\n    font-weight: 400;\n    margin: 0 40px 12px 0;\n}\n\n.accordion .accordion-content {\n    background-color: white;\n    max-height: 0;\n    overflow: hidden;\n    transition: max-height 0.2s ease-out;\n}\n\n.accordion .down {\n    display: block;\n}\n\n.accordion .symbol {\n    position: absolute;\n    top: 24px;\n    right: 16px;\n    cursor: pointer;\n    pointer-events: none;\n}\n\n.accordion.show .symbol {\n    transform: rotate(180deg);\n    top: 27px;\n}\n\n.accordion button {\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: inherit;\n    font-weight: inherit;\n    display: block;\n    width: 100%;\n    border: none;\n    outline-offset: -5px 0;\n    background: transparent;\n    cursor: pointer;\n    margin: 0 0 0.5rem 0;\n    font-weight: 600;\n    padding: 22px 40px 22px 0;\n    text-align: left;\n    -webkit-touch-callout: none;\n    /* iOS Safari */\n    -webkit-user-select: none;\n    /* Safari */\n    -moz-user-select: none;\n    /* Firefox */\n    -ms-user-select: none;\n    /* Internet Explorer/Edge */\n    user-select: none;\n    /* Non-prefixed version, currently\n                    supported by Chrome and Opera */\n}\n\n.accordion .has-subtitle {\n    padding-bottom: 11px;\n}\n\n.accordion-divider {\n    border-top: 1px solid #eaeced;\n}\n\n#financing-terms {\n    position: relative;\n}\n\n#loading-image {\n    position: absolute;\n    left: 50%;\n    top: 50%;\n    border-radius: 50%;\n    border-top: 4px solid #00ff7f;\n    width: 80px;\n    height: 80px;\n    opacity: 0;\n    -webkit-animation: spin 2s linear infinite;\n    animation: spin 2s linear infinite;\n    transition: opacity 0.2s;\n    pointer-events: none;\n    transform-origin: 0 0;\n    z-index: 5;\n}\n\n@media only screen and (max-width: 400px) {\n    #ni-content .title {\n        font-size: 1.6rem;\n        line-height: 2rem;\n        max-width: 14rem;\n        margin-left: auto;\n        margin-right: auto;\n    }\n}\n\n@media only screen and (max-width: 612px) {\n    main .content {\n        padding: 0 18px;\n    }\n}\n\n@media only screen and (max-device-width: 612px) {\n    main {\n        max-width: 100%;\n        -webkit-text-size-adjust: none;\n    }\n}\n\n/* FIREFOX ONLY TARGETING */\n@-moz-document url-prefix() {\n    .modal__header-container {\n        overflow-y: scroll;\n        display: block !important;\n    }\n}\n\n@-webkit-keyframes spin {\n    0% {\n        -webkit-transform: rotate(0deg) translate(-50%, -50%);\n    }\n    100% {\n        -webkit-transform: rotate(360deg) translate(-50%, -50%);\n    }\n}\n\n@keyframes spin {\n    0% {\n        transform: rotate(0deg) translate(-50%, -50%);\n    }\n    100% {\n        transform: rotate(360deg) translate(-50%, -50%);\n    }\n}\n";