export default "section {\n    overflow-x: hidden;\n}\n\n.content__disclosure {\n    color: #687173;\n    font-size: 0.75rem;\n}\n\n.carousel__title {\n    font-size: 20px;\n    font-weight: 300;\n    margin-top: 0;\n    margin-bottom: 0;\n    text-align: center;\n}\n\n.calculator__title {\n    /* font-family: PayPalSansBig, Helvetica, Arial, Sans-Serif; */\n    text-align: center;\n}\n\n.calculator {\n    text-align: center;\n    max-width: 300px;\n    margin: auto;\n}\n\n.calculator__instructions {\n    color: #687173;\n    position: relative;\n    left: 50%;\n    transform: translateX(-50%);\n    width: 300px;\n}\n\n.calculator__instructions::before {\n    content: '';\n    display: block;\n    position: absolute;\n    top: -0.7rem;\n    width: 220px;\n    left: 50%;\n    transform: translateX(-50%);\n    border-bottom: 1px dotted #cbd2d6;\n}\n\n.calculator__input {\n    font-size: 20px;\n    text-align: center;\n    width: 100%;\n    border: none;\n}\n\n.calculator__form {\n    width: 150px;\n    margin: auto;\n    padding: 0 2rem;\n    font-size: 20px;\n    position: relative;\n}\n\n.calculator__form:after {\n    content: '';\n    position: absolute;\n    top: 1px;\n    right: 1.3rem;\n}\n\n.calculator__btn {\n    font-size: 10px;\n    cursor: pointer;\n    border-radius: 50px;\n    background-color: #0070ba;\n    border: solid 3px #0070ba;\n    color: white;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: 15px;\n    padding: 5px 20px;\n}\n\n.carousel {\n    max-width: 400px;\n    margin: auto;\n    overflow: hidden;\n    position: relative;\n    width: 66.666666%;\n}\n\n.carousel__inner {\n    white-space: nowrap;\n    transition: transform 0.2s ease-out;\n    font-size: 0;\n    transform: translateX(0);\n}\n\n.carousel__item {\n    display: inline-block;\n    width: 100%;\n    padding: 15px 30px 0;\n    box-sizing: border-box;\n    white-space: normal;\n}\n\n.carousel__item img {\n    display: block;\n    height: auto;\n    max-width: 100%;\n}\n\n.carousel__image {\n    margin: auto;\n}\n\n.carousel__text {\n    text-align: center;\n    max-width: 250px;\n    font-weight: 600;\n    margin-left: auto;\n    margin-right: auto;\n}\n\n.carousel__indicators {\n    padding: 0;\n    text-align: center;\n    position: relative;\n    z-index: 2;\n    margin-top: 0;\n}\n\n.carousel__indicators li {\n    display: inline-block;\n    margin: 0;\n}\n\n.carousel__bullet {\n    cursor: pointer;\n    display: block;\n    border: none;\n    background: none;\n    height: 30px;\n    width: 30px;\n    position: relative;\n    overflow: hidden;\n}\n\n.carousel__bullet:after {\n    background-color: #aaaaaa;\n    content: '';\n    width: 10px;\n    height: 10px;\n    border-radius: 50%;\n    position: absolute;\n    top: 10px;\n    left: 10px;\n}\n\n.carousel__bullet.active:after {\n    background-color: #0070ba;\n}\n\n.carousel__shadow {\n    position: absolute;\n    height: 100%;\n    width: 100%;\n    box-shadow: 0px -20px 20px 20px #f6f7fb inset;\n    z-index: 1;\n}\n\n.carousel__background {\n    border-radius: 0 0 100% 100%;\n    width: 150%;\n    background: #f6f7fb;\n    overflow: auto;\n    margin-left: -25%;\n}\n\n.carousel__arrows {\n    width: 100%;\n    position: absolute;\n    z-index: 2;\n    top: 10px;\n}\n\n.carousel__arrows > button {\n    border-radius: 100%;\n    width: 3rem;\n    height: 3rem;\n    background: none;\n    border: none;\n    position: absolute;\n    cursor: pointer;\n}\n\n.hidden {\n    display: none;\n}\n\n.carousel__arrows > button:hover {\n    background: #eee;\n}\n\n.carousel__arrows > button:focus {\n    background: #eee;\n    outline: none;\n    border: 1px solid rgba(0, 112, 186, 0.5);\n}\n\n.carousel__arrows > button:after {\n    content: '';\n    position: absolute;\n    width: 30%;\n    height: 30%;\n    border-top: 2px solid #cbd2d6;\n    border-right: 2px solid #cbd2d6;\n    top: 37%;\n    left: 28%;\n}\n\n.carousel__arrow-prev {\n    left: 10px;\n    transform: rotate(-135deg);\n}\n\n.carousel__arrow-next {\n    right: 10px;\n    transform: rotate(45deg);\n}\n\nheader {\n    background: #f6f7fb;\n}\n\n.modal__content {\n    padding-top: 72px;\n}\n\n#logo {\n    width: 200px;\n}\n\n.terms table {\n    width: 100%;\n    font-size: 14px;\n}\n\n.terms td {\n    padding: 2px 0;\n}\n\n.terms td:last-child {\n    text-align: right;\n}\n\n.terms__header {\n    text-align: center;\n}\n\n.terms__error {\n    text-align: center;\n}\n\nhr {\n    opacity: 0.3;\n}\n\n.terms__loading {\n    bottom: 100px;\n}\n\n.disclosure {\n    color: #687173;\n    font-size: 12px;\n}\n\n@media only screen and (min-width: 436px) {\n    .terms td {\n        padding: 5px 0;\n    }\n\n    .calculator__title {\n        font-size: 19px;\n    }\n\n    .calculator__form {\n        font-size: 24px;\n    }\n\n    .calculator__form:after {\n        top: -1px;\n    }\n\n    .calculator__input {\n        font-size: 24px;\n    }\n}\n";