export default "main #tabs {\n    margin: auto;\n    max-width: 400px;\n    padding: 0 18px;\n    margin-bottom: 28px;\n    border-bottom: 1px solid #eaeced;\n    white-space: nowrap;\n    font-size: 0;\n}\n\nmain #tabs button {\n    cursor: pointer;\n    display: inline-block;\n    padding: 30px 0px 15px;\n    font-size: 15px;\n    font-weight: 600;\n    margin-bottom: 0;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    width: 50%;\n    text-align: center;\n    margin-top: 0;\n    white-space: normal;\n    background: transparent;\n    border: none;\n    outline: none;\n}\n\nmain #tabs button:hover {\n    color: #0070ba;\n}\n\nmain #tabs button:active {\n    color: #005187;\n}\n\nmain #tabs button.selected {\n    color: #0070ba;\n    border-bottom: 3px solid #0070ba;\n}\n\n/* main .content {\n    display: none;\n} */\n\nmain .content.show {\n    display: block;\n}\n\nmain #financing-terms table {\n    border-collapse: collapse;\n    width: 100%;\n    position: relative;\n}\n\nmain #financing-terms thead tr {\n    height: 50px;\n    padding: 5px 0 0 5px;\n    background: #f5f5f5;\n    font-weight: 600;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    margin-bottom: 0;\n    font-size: 13px;\n}\n\nmain #financing-terms thead tr th {\n    padding-top: 5px;\n    text-align: left;\n    padding-left: 8px;\n    vertical-align: top;\n    line-height: 1.38;\n    letter-spacing: -0.2px;\n    color: #2c2e2f;\n}\n\nmain #financing-terms tbody tr:not(:first-of-type) {\n    border-top: solid 0.5px #eaeced;\n    clear: both;\n    width: 99.99%;\n}\n\nmain #financing-terms tbody tr:last-of-type {\n    margin-bottom: 38px;\n}\n\nmain #financing-terms tbody tr td {\n    padding: 7px 0 7px 5px;\n    box-sizing: border-box;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    margin: 0;\n    padding-left: 8px;\n    font-size: 13px;\n    font-weight: 400;\n    letter-spacing: -0.2px;\n    color: #2c2e2f;\n}\n\n#ezp-content .title {\n    padding: 0 32px;\n    margin-top: 13px;\n    margin-bottom: 32px;\n}\n\n.calculator {\n    margin: 10px 0px;\n}\n\n.calculator__form {\n    position: relative;\n    display: -ms-flexbox;\n    display: flex;\n    box-sizing: border-box;\n}\n\n.calculator__form::before {\n    content: '$';\n    position: absolute;\n    display: block;\n    z-index: 9;\n    color: black;\n    top: 50%;\n    left: 14px;\n    transform: translateY(-50%);\n}\n\n.calculator__input {\n    flex: 1 1 70%;\n    -ms-flex: 1 1 70%;\n    border: 1px solid #b7bcbf;\n    border-radius: 5px;\n    background: white;\n    padding: 10px 10px 10px 25px;\n    /*outline: none;*/\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: 15px;\n    font-weight: 400;\n    font-style: normal;\n    font-stretch: normal;\n    line-height: 1.6;\n    letter-spacing: normal;\n    color: #2c2e2f;\n    box-sizing: border-box;\n    min-width: 0;\n}\n\n.calculator__btn {\n    flex: 1 1 20%;\n    -ms-flex: 1 1 20%;\n    padding: 0 14px;\n    max-width: 120px;\n    cursor: pointer;\n    border-radius: 50px;\n    background-color: #0070ba;\n    border: solid 1px #0070ba;\n    color: white;\n    margin-left: 10px;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: 15px;\n    font-weight: 400;\n    font-style: normal;\n    font-stretch: normal;\n    line-height: normal;\n    letter-spacing: 0.7px;\n    text-align: center;\n    display: block;\n    box-sizing: border-box;\n}\n\n.calculator__error-msg {\n    display: none;\n    border: 2px solid #d20000;\n    padding: 10px;\n    text-align: center;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-size: 13px;\n    font-weight: normal;\n    font-style: normal;\n    font-stretch: normal;\n    line-height: 1.85;\n    letter-spacing: normal;\n    color: #2c2e2f;\n    background: #fff7f7;\n    border-radius: 6px;\n    margin-top: 10px;\n}\n\ninput[type='number']::-webkit-inner-spin-button,\ninput[type='number']::-webkit-outer-spin-button {\n    margin: 0;\n    -webkit-appearance: none;\n    -moz-appearance: textfield;\n}\n\ninput:invalid + p {\n    visibility: visible;\n    color: red;\n}\n\ninput:valid + p {\n    visibility: hidden;\n}\n\nmain #how-it-works {\n    position: relative;\n    bottom: 56px;\n}\n\n#terms-note {\n    display: block;\n    margin-top: 15px;\n    margin-bottom: 20px;\n    font-size: 13px;\n    font-weight: 400;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    color: #2c2e2f;\n}\n\n#terms-error {\n    font-size: 13px;\n    margin-bottom: 24px;\n}\n\n#ezp-content h3 {\n    font-size: 15px;\n    font-weight: 500;\n    line-height: 1.6;\n    font-family: PayPalSansSmall, Helvetica, Arial, Sans-Serif;\n    font-style: normal;\n    font-weight: 600;\n    font-stretch: normal;\n    letter-spacing: normal;\n}\n\n@media only screen and (max-width: 400px) {\n    #ezp-content .title {\n        font-size: 1.6rem;\n        line-height: 2rem;\n        padding: 0;\n    }\n}\n";